$(".js-hero-slider").slick({
  arrows: true,
  autoplay: true,
  fade: true,
  cssEase: "linear",
  dots: true,
  nextArrow:
    '<button type="button" class="hero-slider__btn"></button>',
});

$(".js-vr-slider").slick({
  arrows: false,
  variableWidth: true,
});

$(".js-ps-slider").slick({
  arrows: false,
  variableWidth: true,
});

$(".js-service-slider").slick({
  arrows: false,
  variableWidth: true,
});

$(".js-comments-slider").slick({
  arrows: true,
  variableWidth: true,
  prevArrow:
    '<button type="button" class="comments-slider__btn comments-slider__btn--prev"></button>',
  nextArrow:
    '<button type="button" class="comments-slider__btn comments-slider__btn--next"></button>',
});
